@import url(https://fonts.googleapis.com/css?family=Rancho&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-color: #1d1d1d;
}
.main {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.main-container {
  display: flex;
  justify-content: space-evenly;
}
.task-title {
  font-size: 40px;
  font-weight: bolder;
  color: #fae607;
}

.copy-tool-new,
.delete-tool-new {
  width: 25px;
  height: 25px;
  position: absolute;
  cursor: pointer;
}
.copy-tool-new {
  background-image: url(data:image/png;base64,);
  background-size: contain;
  background-repeat: no-repeat;
}
.delete-tool-new {
  background-image: url(data:image/png;base64,);
  background-size: contain;
  background-repeat: no-repeat;
}
.copy-tool-new {
  left: 5%;
  top: 75%;
}
.delete-tool-new {
  left: 76%;
  top: 75%;
}

#mini-canvas {
  width: 120px;
  height: 120px;
  box-sizing: border-box;
  -ms-interpolation-mode: nearest-neighbor;
      image-rendering: -moz-crisp-edges;
      image-rendering: pixelated;
}
.canvas {
  position: relative;
  background-image: url(data:image/png;base64,);
}
.frame-container {
  width: 150px;
  height: 100%;
}
.frame {
  width: 120px;
  height: 120px;
  margin: 27px;
  position: relative;
}
.create-new-frame {
  width: 120px;
  height: 30px;
  margin-left: 26px;
  background-image: -webkit-linear-gradient(30deg, #29323c 0%, #485563 100%);
  background-image: linear-gradient(60deg, #29323c 0%, #485563 100%);
  padding-top: 5px;
  border-radius: 5px;
  text-align: center;
  color: #fae607;
  font-size: 1.3em;
  box-shadow: 0 0 10px #fae607;
  cursor: pointer;
}
.copy-tool,
.delete-tool,
.copy-tool-new,
.delete-tool-new {
  width: 25px;
  height: 25px;
  position: absolute;
  cursor: pointer;
}
.copy-tool {
  background-image: url(data:image/png;base64,);
  background-size: contain;
  background-repeat: no-repeat;
}
.delete-tool {
  background-image: url(data:image/png;base64,);
  background-size: contain;
  background-repeat: no-repeat;
}
.copy-tool {
  left: 5%;
  bottom: 5%;
}
.delete-tool {
  left: 76%;
  bottom: 5%;
}
.active {
  border: 4px solid #fae607;
}

#main-canvas {
  width: 38vw;
  height: 38vw;
  margin: 27px 27px 0 27px;
  -ms-interpolation-mode: nearest-neighbor;
      image-rendering: -moz-crisp-edges;
      image-rendering: pixelated;
}
.canvas {
  position: relative;
  background-image: url(data:image/png;base64,);
}

.animation-canvas {
  width: 300px;
  height: 300px;
  background-image: url(data:image/png;base64,);
  -ms-interpolation-mode: nearest-neighbor;
      image-rendering: -moz-crisp-edges;
      image-rendering: pixelated;
  margin: 27px 0;
}
.animation-canvas:-webkit-full-screen {
  background-color: white;
}
.animation-canvas:-moz-full-screen {
  background-color: white;
}
.animation-canvas:-ms-fullscreen {
  background-color: white;
}
.animation-canvas:fullscreen {
  background-color: white;
}
.animation {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.fullscreen-icon {
  position: absolute;
  top: 290px;
  left: 263px;
  font-size: 30px;
  color: #fae607;
  cursor: pointer;
}
.input-animation {
  cursor: pointer;
}
.input-label {
  color: #fae607;
  font-size: 20px;
}

.coordinate-component {
  color: #fae607;
  font-size: 20px;
  text-align: center;
  margin-top: 50px;
}

.tools-component {
  display: grid;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-template-columns: 1fr 1fr;
  grid-gap: 0 4px;
  height: 400px;
  margin: 60px 0 0 2px;
}
.tools-component-button {
  margin: 0;
  padding: 0;
  width: 45px;
  height: 45px;
  font-size: 25px;
  background-color: #444;
  color: #ccc;
  border: none;
  outline: none;
  cursor: pointer;
}
.tools-component-input {
  position: absolute;
  opacity: 0;
  width: 40px;
  height: 41px;
  cursor: pointer;
}
.tools-component-palette {
  color: #000;
}
.palette {
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
}
.tools-size {
  display: flex;
  justify-content: space-around;
  width: 98px;
  margin-top: 27px;
}
.tools-size-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20px;
  height: 20px;
  border: 1px solid white;
  background-color: #fff;
  position: relative;
  left: 100%;
  box-sizing: border-box;
  cursor: pointer;
}
.tools-size-mini,
.tools-size-normal,
.tools-size-big,
.tools-size-huge {
  background-color: #000;
}
.tools-size-mini {
  width: 7px;
  height: 7px;
}
.tools-size-normal {
  width: 10px;
  height: 10px;
}
.tools-size-big {
  width: 13px;
  height: 13px;
}
.tools-size-huge {
  width: 16px;
  height: 16px;
}
.active-size,
.active-tool {
  border: 3px solid #fae607;
}
.mirror {
  font-size: 35px;
}
.circle,
.square {
  display: flex;
  justify-content: center;
  align-items: center;
}
.inner-square,
.inner-circle {
  width: 18px;
  height: 18px;
  border: 4px solid #ccc;
}
.inner-circle {
  border-radius: 50%;
}
.inner-pen {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  font-size: 22px;
}
.tools-component-pen {
  -webkit-transform: rotate(-15deg);
          transform: rotate(-15deg);
  position: relative;
  top: 5px;
}
.inner-line {
  width: 30px;
  height: 15px;
  border-bottom: 2px solid #ccc;
  -webkit-transform: rotate(-20deg);
          transform: rotate(-20deg);
  position: relative;
  bottom: 10px;
}
.chess-board-button,
.all-pixels-same-color {
  display: flex;
  justify-content: center;
  align-items: center;
}
.tools-chess-board {
  width: 30px;
  height: 30px;
  background-image: url(/animation-player/static/media/chessboard.e8432507.png);
  background-repeat: no-repeat;
  background-size: 100%;
}
.move-tool,
.lighten-tool {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 26px;
}
.lighten-tool-plus,
.lighten-tool-minus {
  font-size: 15px;
  margin-bottom: 8px;
}
.same-color-filldrip {
  font-size: 15px;
}

* {
  font-family: 'Rancho', cursive;
}
ul,
li {
  list-style-type: disc;
}

.landing-page {
  display: flex;
  width: 100%;
  justify-content: space-between;
  flex-direction: row-reverse;
}

.landing-page-screenshot {
  height: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  flex-direction: column;
  margin: 20px;
}
.link-to-main-page {
  font-size: 35px;
  text-decoration: none;
  margin-bottom: 20px;
  width: 200px;
  height: 45px;
  background-image: -webkit-gradient(linear, left top, right top, from(#f83600), to(#f9d423));
  background-image: -webkit-linear-gradient(left, #f83600 0%, #f9d423 100%);
  background-image: linear-gradient(to right, #f83600 0%, #f9d423 100%);
  border-radius: 15px;
  box-shadow: 0 0 10px 5px blue;
  text-align: center;
}
.example-functional-image {
  width: 600px;
  height: 300px;
  background-image: url(/animation-player/static/media/example.d3691931.png);
  background-repeat: no-repeat;
  background-size: 100%;
}
.landing-page-overview_functionality {
  margin: 0 0 0 3%;
  color: #fff;
  font-size: 20px;
}
.landing-page-overview_functionality p {
  font-size: 30px;
}
.landing-page-animation-examples {
  display: flex;
  justify-content: center;
  width: 100%;
  margin-bottom: 30px;
}
.landing-page-animation-image {
  margin: 0 10px;
}
.landing-page-author-information {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-size: 24px;
}
.landing-page-author-information p {
  margin-right: 10px;
}
.landing-page-footer-img {
  margin: 3px 5px 0 0;
}
.landing-page-component {
  height: 100vh;
}

